// hooks/useProductosConPromo.ts
import { useEffect, useState } from "react";

export interface Producto {
  id: number;
  nombre: string;
  precio: number;
  imagen: string;
  etiquetas: string[];
  descripcion: string;
  opciones?: {
    color?: string[];
    sabor?: string[];
    tama√±o?: string[];
  };
  promo?: Promo | undefined;
}

export interface Promo {
  id: number;
  tipo: "descuento" | "2x1" | "nuevo" | "solo-hoy";
  valor?: number;
  nombre: string;
  descripcion: string;
  diasSemana: number[];
}

interface PromoProductoRel {
  idProducto: number;
  idPromo: number;
}

/**
 * Helper gen√©rico para hacer fetch y parsear JSON
 */
async function fetchJSON<T>(url: string): Promise<T> {
  const res = await fetch(url);
  if (!res.ok) throw new Error(`Error en ${url}: ${res.status}`);
  return res.json() as Promise<T>;
}

/**
 * Hook que devuelve el listado de productos, con su promo (si aplica).
 */
export default function useProductosConPromo() {
  const [productos, setProductos] = useState<Producto[]>([]);

  useEffect(() => {
    let mounted = true;

    Promise.all([
      fetchJSON<Producto[]>("/api/productos"),
      fetchJSON<Promo[]>("/api/promos"),
      fetchJSON<PromoProductoRel[]>("/api/promo-productos"),
    ])
      .then(([rawProductos, promos, rels]) => {
        if (!mounted) return;

        // Asociar cada producto con su promo (la primera que encuentre)
        const productosConPromo = rawProductos.map((prod) => {
          const rel = rels.find((r) => r.idProducto === prod.id);
          const promo = rel ? promos.find((p) => p.id === rel.idPromo) : undefined;
          return { ...prod, promo };
        });

        setProductos(productosConPromo);
      })
      .catch((err) => {
        console.error("Error cargando productos/promos:", err);
        // opcional: podr√≠as setear un estado de error aqu√≠
      });

    return () => {
      mounted = false;
    };
  }, []);

  return productos;
}
