// components/catalogo/OptionSelect.tsx
"use client";

type Props = {
  label: string;
  value: string;
  options: (string | number)[];
  onChange: (v: string) => void;
};

/**
 * **OptionSelect**
 *
 * Selector gen√©rico (HTML `<select>`) para escoger variantes de producto
 * (tama√±o, sabor, etc.).
 */
export default function OptionSelect({ label, value, options, onChange }: Props) {
  return (
    <div className="space-y-1">
      <label className="block font-semibold capitalize dark:text-gray-800">{label}</label>

      <div className="relative">
        <select
          value={value}
          onChange={(e) => onChange(e.target.value)}
          className="w-full appearance-none border border-gray-300 dark:border-gray-600
                     bg-white dark:[background-color:#ffd68a] text-gray-800 dark:text-gray-800
                     rounded-lg px-3 py-2 pr-10 focus:ring-2 focus:ring-green-400 focus:outline-none"
        >
          {options.map((v) => (
            <option key={v}>{v}</option>
          ))}
        </select>

        {/* flecha */}
        <svg className="pointer-events-none absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-600" fill="currentColor" viewBox="0 0 20 20">
          <path d="M7 7l3 3 3-3" stroke="currentColor" strokeWidth={2} fill="none" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
      </div>
    </div>
  );
}
