"use client";

import { useMemo, useState, useEffect } from "react";
import UseProductosPromo, {
    type Producto,
} from "@/components/useProductosPromo";

import ProductCard from "./components/ProductCard";
import SearchBar from "./components/SearchBar";
import FilterButton from "./components/FilterButton";
import FilterModal from "./components/FilterModal";
import ProductDetailModal from "./components/ProductDetailModal";
import { useCart } from "@/lib/cart-context";

/* üëâ array de rese√±as para calcular promedios */
import { obtenerRese√±as } from "./data/reviews";

/* helpers */
const getDefaultOpts = (
    p: Producto
): Record<string, string | number> | undefined => {
    if (!p.opciones) return undefined;
    const def: Record<string, string | number> = {};
    for (const [k, vals] of Object.entries(p.opciones)) {
        if (Array.isArray(vals) && vals.length) def[k] = vals[0];
    }
    return Object.keys(def).length ? def : undefined;
};

export default function CatalogoPage() {
    const productos = UseProductosPromo();
    const [allReviews, setAllReviews] = useState<
        Awaited<ReturnType<typeof obtenerRese√±as>>
    >([]);

    useEffect(() => {
        (async () => {
            const reviews = await obtenerRese√±as();
            setAllReviews(reviews);
        })();
    }, []);

    /* ---------- filtros de UI ---------- */
    const [min, setMin] = useState(0);
    const [max, setMax] = useState(0);
    const [search, setSearch] = useState("");
    const [tag, setTag] = useState<string | null>(null);

    const [promos, setPromos] = useState(false);
    const [nuevos, setNuevos] = useState(false);
    const [rating45, setRating45] = useState(false);

    const [showFilters, setShowFilters] = useState(false);
    const [details, setDetails] = useState<Producto | null>(null);

    /* rango precio inicial */
    useEffect(() => {
        if (productos.length) {
            const precios = productos.map((p) => p.precio);
            setMin(Math.min(...precios));
            setMax(Math.max(...precios));
        }
    }, [productos]);

    /* ---------- carrito ---------- */
    const { addItem } = useCart();

    const addSimple = (p: Producto) =>
        addItem({
            id: String(p.id),
            name: p.nombre,
            price: p.precio,
            image: p.imagen,
            quantity: 1,
            options: getDefaultOpts(p),
            promo: p.promo,
        });

    const addWithOpts = (
        p: Producto,
        opts: Record<string, string | number>,
        qty: number
    ) =>
        addItem({
            id: String(p.id),
            name: p.nombre,
            price: p.precio,
            image: p.imagen,
            quantity: qty,
            options: opts,
            promo: p.promo,
        });

    /* ---------- filtrado ---------- */
    const list = useMemo(() => {
        const term = search.trim().toLowerCase();
        return productos.filter((p) => {
            /* b√°sicos */
            const okName = !term || p.nombre.toLowerCase().includes(term);
            const okTag = !tag || p.etiquetas.includes(tag);
            const okP = p.precio >= min && p.precio <= max;

            /* nuevos criterios */
            const okPromos = !promos || !!p.promo;
            const okNuevos = !nuevos || p.promo?.tipo === "nuevo";

            /* rating medio */
            const reviews = allReviews.filter((r) => r.productoId === p.id);
            const avg = reviews.length
                ? reviews.reduce((s, r) => s + r.calificacion, 0) /
                  reviews.length
                : 0;
            const okRating = !rating45 || avg >= 4.5;

            return okName && okTag && okP && okPromos && okNuevos && okRating;
        });
    }, [
        productos,
        search,
        tag,
        min,
        max,
        promos,
        nuevos,
        rating45,
        allReviews,
    ]);

    /* etiquetas √∫nicas */
    const allTags = useMemo(
        () => Array.from(new Set(productos.flatMap((p) => p.etiquetas))),
        [productos]
    );

    /* reset */
    const reset = () => {
        setSearch("");
        setTag(null);
        setPromos(false);
        setNuevos(false);
        setRating45(false);
        if (productos.length) {
            const precios = productos.map((p) => p.precio);
            setMin(Math.min(...precios));
            setMax(Math.max(...precios));
        }
    };

    /* loader */
    if (!productos.length) {
        return (
            <main className="min-h-screen flex items-center justify-center">
                <span className="text-2xl text-gray-500">
                    Cargando productos‚Ä¶
                </span>
            </main>
        );
    }

    /* UI */
    return (
        <main className="min-h-screen bg-yellow-50 dark:bg-gray-800 py-10 px-6">
            <h1 className="text-4xl font-extrabold text-green-500 dark:text-yellow-500 text-center mb-4">
                üõí Productos del Kwik-E-Mart
            </h1>
            <p className="text-center text-lg text-gray-700 dark:text-white mb-6">
                Explora nuestra variedad de productos y a√±ade tus favoritos al
                carrito.
            </p>

            {/* buscador + filtros */}
            <SearchBar value={search} onChange={setSearch} />
            <FilterButton onClick={() => setShowFilters(true)} />

            {list.length === 0 ? (
                <p className="text-center text-gray-600">
                    No encontramos productos.
                </p>
            ) : (
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 max-w-6xl mx-auto">
                    {list.map((p) => (
                        <ProductCard
                            key={p.id}
                            producto={p}
                            onAdd={addSimple}
                            onSelect={setDetails}
                        />
                    ))}
                </div>
            )}

            {/* modal filtros */}
            <FilterModal
                show={showFilters}
                onClose={() => setShowFilters(false)}
                etiquetas={allTags}
                etiquetaActiva={tag}
                setEtiqueta={setTag}
                precioMin={min}
                setPrecioMin={setMin}
                precioMax={max}
                setPrecioMax={setMax}
                limpiar={reset}
                /* nuevos flags */
                promos={promos}
                setPromos={setPromos}
                rating45={rating45}
                setRating45={setRating45}
                nuevos={nuevos}
                setNuevos={setNuevos}
            />

            {/* modal detalle */}
            <ProductDetailModal
                show={Boolean(details)}
                product={details}
                onClose={() => setDetails(null)}
                onAdd={addWithOpts}
            />
        </main>
    );
}
