/**
 * useEscapeKey
 * -----------------------------------------------------------------------------
 * Detecta la pulsaci√≥n de la tecla **Escape** y ejecuta un callback.
 * Muy √∫til para cerrar modales, sidebars o pop-ups con el teclado.
 *
 * @param onEscape ‚Äî Funci√≥n que se dispara al presionar Esc.
 * @param enabled  ‚Äî Si es `false`, el listener no se registra.
 *
 * Detalles:
 *   ‚Ä¢ El listener se adjunta a `window` √∫nicamente cuando `enabled` es `true`.
 *   ‚Ä¢ El efecto limpia el listener autom√°ticamente al desmontar o cuando
 *     `enabled` cambia, evitando fugas de memoria.
 */
import { useEffect } from "react"

export default function useEscapeKey(
  onEscape: () => void,
  enabled: boolean
) {
  useEffect(() => {
    if (!enabled) return                     // Early-exit si no est√° activo

    const handler = (e: KeyboardEvent) => {
      if (e.key === "Escape") onEscape()
    }

    window.addEventListener("keydown", handler)

    /* Limpieza ************************************************************* */
    return () => window.removeEventListener("keydown", handler)
  }, [enabled, onEscape])
}
