"use client";

import { useEffect } from "react";

interface Props {
  show: boolean;
  onClose: () => void;

  // filtros existentes
  etiquetas: string[];
  etiquetaActiva: string | null;
  setEtiqueta: (e: string | null) => void;
  precioMin: number;
  precioMax: number;
  setPrecioMin: (n: number) => void;
  setPrecioMax: (n: number) => void;
  limpiar: () => void;

  // üëâ NUEVOS FLAGS
  promos: boolean;
  setPromos: (b: boolean) => void;
  rating45: boolean;
  setRating45: (b: boolean) => void;
  nuevos: boolean;
  setNuevos: (b: boolean) => void;
}

export default function FilterModal({
  show,
  onClose,
  etiquetas,
  etiquetaActiva,
  setEtiqueta,
  precioMin,
  precioMax,
  setPrecioMin,
  setPrecioMax,
  limpiar,
  promos,
  setPromos,
  rating45,
  setRating45,
  nuevos,
  setNuevos,
}: Props) {
  /* bloqueo scroll */
  useEffect(() => {
    document.body.style.overflow = show ? "hidden" : "auto";
  }, [show]);

  if (!show) return null;

  /* helper para clases de chip */
  const chipClass = (active: boolean) =>
    active
      ? "px-3 py-1.5 rounded-full border text-sm bg-green-500 text-white"
      : "px-3 py-1.5 rounded-full border text-sm border-gray-300 bg-gray-100 text-gray-800 dark:border-amber-200 dark:bg-amber-100";

  return (
    <div className="fixed inset-0 z-50 flex items-start justify-center p-4 sm:pt-12">
      <button aria-label="Cerrar filtros" onClick={onClose} className="absolute inset-0 bg-gray-900/40 backdrop-blur-sm" />

      <div className="relative w-full max-w-lg max-h-[90vh] overflow-y-auto rounded-2xl shadow-xl bg-white dark:bg-amber-100">
        {/* header */}
        <div className="p-4 flex items-center justify-between border-b border-gray-200 dark:border-amber-100 dark:text-black">
          <h2 className="text-xl font-bold">Filtrar por:</h2>
          <button className="p-2 rounded-full hover:bg-gray-100 dark:hover:bg-amber-50" onClick={onClose}>
            ‚úï
          </button>
        </div>

        {/* contenido */}
        <div className="p-6 space-y-8 bg-white dark:bg-yellow-500 rounded-b-2xl">
          {/* chips r√°pidos */}
          <div className="flex flex-wrap gap-2">
            <button onClick={() => setPromos(!promos)}   className={chipClass(promos)}>üåü Promos</button>
            <button onClick={() => setRating45(!rating45)} className={chipClass(rating45)}>‚≠ê +4.5</button>
            <button onClick={() => setNuevos(!nuevos)}   className={chipClass(nuevos)}>üõí ¬°Nuevos!</button>
          </div>

          {/* categor√≠as */}
          <div>
            <h3 className="mb-2 font-semibold text-gray-800 dark:text-gray-800">Categor√≠as</h3>
            <div className="flex flex-wrap gap-2">
              {etiquetas.map((cat) => {
                const active = etiquetaActiva === cat;
                return (
                  <button
                    key={cat}
                    onClick={() => setEtiqueta(active ? null : cat)}
                    className={
                      active
                        ? "px-4 py-1.5 rounded-full text-sm capitalize border border-green-600 bg-green-500 text-white"
                        : "px-4 py-1.5 rounded-full text-sm capitalize border border-gray-300 bg-white text-gray-700 hover:bg-gray-100 dark:border-amber-300 dark:bg-amber-100 dark:text-gray-800"
                    }
                  >
                    {cat}
                  </button>
                );
              })}
            </div>
          </div>

          {/* rango precio */}
          <div>
            <h3 className="mb-2 font-semibold text-gray-800 dark:text-gray-800">Precio ($ USD)</h3>
            <div className="flex flex-wrap gap-4">
              <div className="flex items-center gap-2">
                <label className="text-sm dark:text-gray-800">M√≠n:</label>
                <input
                  type="number"
                  value={precioMin}
                  onChange={(e) => setPrecioMin(Number(e.target.value))}
                  className="w-24 px-3 py-1.5 text-center rounded-lg border border-gray-300 bg-white text-gray-800 focus:ring-2 focus:ring-green-500 dark:border-amber-300 dark:bg-amber-50 dark:text-gray-800"
                />
              </div>
              <div className="flex items-center gap-2">
                <label className="text-sm dark:text-gray-800">M√°x:</label>
                <input
                  type="number"
                  value={precioMax}
                  onChange={(e) => setPrecioMax(Number(e.target.value))}
                  className="w-24 px-3 py-1.5 text-center rounded-lg border border-gray-300 bg-white text-gray-800 focus:ring-2 focus:ring-green-500 dark:border-amber-300 dark:bg-amber-100 dark:text-gray-800"
                />
              </div>
            </div>
          </div>

          {/* acciones */}
          <div className="flex gap-4 pt-2 border-t border-gray-200 dark:border-amber-300">
            <button onClick={limpiar} className="flex-1 py-2 rounded-xl border border-green-600 text-green-600 hover:bg-green-50 dark:bg-amber-100 dark:text-gray-800">
              Limpiar
            </button>
            <button onClick={onClose} className="flex-1 py-2 rounded-xl bg-green-500 hover:bg-green-600 text-white dark:text-gray-800">
              Aplicar
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
